import React from 'react-ex';
import Table from '../../../Components/APITable';
import ButtonExample from './Else/Button.Ex';
import AddEntryComponent from '../../EntryComponent';

export default class App extends React.Component {
    render() {
        return [
            (
                <style key="xx">{
                    `[class="code-box-demo"] .ant-btn {
                    margin-right: 8px;
                    margin-bottom: 12px;
                    }
                    [class="code-box-demo"] .ant-btn-group > .ant-btn {
                    margin-right: 0;
                    }`
                }</style>
            ),
            (
                <section className="markdown">
                    <h1>Button<span className="subtitle"></span></h1>
                    <section className="markdown">
                        <p></p>
                        <h2 id="" data-scrollama-index="0">
                            <span></span>
                            <a href="#" className="anchor">#</a>
                        </h2>
                        <p></p>
                    </section>
                    <h2 data-scrollama-index="1">
                        <span></span>
                        <i className="anticon anticon-appstore code-box-expand-trigger" title="" />
                    </h2>
                    <ButtonExample />

                </section>
            )
            ,
            (
                <section className="markdown api-container ant-col-20">
                    <h2 id="API" data-scrollama-index="10"><span>API</span>
                        <a href="#API" className="anchor">#</a>
                    </h2>
                    <p> Button 
                    <code>type</code>
                        -{'>'}
                        <code>shape</code>
                        -{'>'}
                        <code>size</code>
                        -{'>'}
                        <code>loading</code>
                        -{'>'}
                        <code>disabled</code>
                    </p>
                    <p></p>
                    <Table
                        datasource={[
                            { property: 'ghost', default: 'false', type: 'boolean', description: ' 2.7 ' },
                            { property: 'htmlType', default: 'button', type: 'string', description: ` <code>button</code>  <code>type</code>  <a href="https://developer.mozilla.org/en-US/docs/Web/HTML/Element/button#attr-type">HTML </a>` },
                            { property: 'icon', default: '-', type: 'string', description: '' },
                            { property: 'loading', default: 'false', type: 'boolean | { delay: number }', description: '' },
                            { property: 'shape', default: '-', type: 'string', description: ` <code>circle</code> ` },
                            { property: 'size', default: 'default', type: 'string', description: ` <code>small</code> <code>large</code> ` },
                            { property: 'type', default: '-', type: 'string', description: ` <code>primary</code> <code>dashed</code> <code>danger</code>( 2.7 ) ` },
                            { property: 'onClick', default: '-', type: 'function', description: `<code>click</code>  handler` },
                            { property: 'href', default: '-', type: 'string', description: ' button  a ' },
                            { property: 'target', default: '-', type: 'string', description: ' a  target href ' }
                        ]}
                    />
                    <p>
                        <code>{`<Button>Hello world!</Button>`}</code>
                        
                    <code>{`<button><span>Hello world!</span></button>`}</code>
                        
                    <code>{`<button></button>`}</code>
                        
                </p>
                    <p>
                        <code>{`<Button href="http://example.com">Hello world!</Button>`}</code>
                        
                    <code>{`<a href="http://example.com"><span>Hello world!</span></a>`}</code>
                        
                </p>
                </section>
            )

        ];
    }
}
AddEntryComponent('Button', 'Document', App);


